<template>
  <page-layout :title="title">
    <template slot="action">
      <div class="interval-sovle">
        <a-button-group style="margin-right: 8px">
          <a-button
            icon="delete"
            @click="batchDelete"
            :loading="loading"
          >
            批量删除
          </a-button>
          <a-button
            icon="export"
            @click="showExportModal"
          >
            导出
          </a-button>
        </a-button-group>
        <a-button
          type="primary"
          icon="search"
          @click="showQueryModel"
        >
          <span>查询</span>
          <queryModal ref="dialogtreeQueryModal"></queryModal>
        </a-button>
      </div>
    </template>
    <div class="systAnnouncement">
      <router-link to="/tools/newsystann">
        <a-button
          type="primary"
          icon="plus"
        >
          <span>新增</span>
        </a-button>
      </router-link>
      <!-- <span style="margin-left: 8px">
      <template v-if="hasSelected">
        {{`Selected ${selectedRowKeys.length} items`}}
      </template>
    </span> -->
      <a-table
        :rowSelection="{selectedRowKeys: selectedRowKeys, onChange: onSelectChange}"
        :dataSource="dataSource"
        :columns="columns"
        @change="handleChange"
        :scroll="{ x: 1300 }"
        :pagination="pagination"
        :loading="loading"
      >
        <template
          slot="operation"
          slot-scope="text, record"
        >
          <a
            href="javascript:;"
            @click="onDelete(record.key)"
          >删除</a>
        </template>
      </a-table>
    </div>
  </page-layout>
</template>

<script>
import ATable from "ant-design-vue/lib/table";
import AButton from "ant-design-vue/lib/button";
import { setTimeout, setImmediate } from "timers";
import queryModal from "./QueryModal";
import PageLayout from "@/components/Page/PageLayout";
const AButtonGroup = AButton.Group
export default {
  props: {
    customerId: String
  },
  components: {
    ATable,
    AButton,
    AButtonGroup,
    queryModal,
    PageLayout
  },
  data() {
    return {
      dataSource: [],
      selectedRowKeys: [],
      loading: false,
      loadingCallHistory: false,
      filteredInfo: null,
      sortedInfo: null,
      pagination: {
        showQuickJumper: true,
        pageSize: 10
      }
    };
  },
  mounted() {
    this.getDataFromServer();
  },
  watch: {
    customerId: {
      handler: function(customerId) {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.deleteLocalHistory();
        }, 1000);
      }
    }
  },
  computed: {
    title() {
      return this.$route.meta.title;
    },
    hasSelected() {
      return this.selectedRowKeys.length > 0;
    },
    columns() {
      let { sortedInfo, filteredInfo } = this;
      sortedInfo = sortedInfo || {};
      filteredInfo = filteredInfo || {};
      const columns = [
        {
          title: "标志",
          dataIndex: "tag",
          key: "tag",
          sorter: (a, b) => a.tag.length - b.tag.length,
          sortOrder: sortedInfo.columnKey === "tag" && sortedInfo.order
        },
        {
          title: "标题",
          dataIndex: "title",
          key: "title",
          sorter: (a, b) => a.title.length - b.title.length,
          sortOrder: sortedInfo.columnKey === "title" && sortedInfo.order
        },
        {
          title: "发布时间",
          dataIndex: "date_create",
          key: "date_create",
          sorter: (a, b) => a.date_create.length - b.date_create.length,
          sortOrder: sortedInfo.columnKey === "date_create" && sortedInfo.order
        },
        {
          title: "发布人",
          dataIndex: "user_create",
          key: "user_create",
          sorter: (a, b) => a.user_create.length - b.user_create.length,
          sortOrder: sortedInfo.columnKey === "user_create" && sortedInfo.order
        },
        {
          title: "操作",
          key: "operation",
          dataIndex: "operation",
          fixed: "right",
          width: 100,
          //使用columns时，可以通过该属性配置支持slot-scope的属性
          scopedSlots: {
            customRender: "operation"
          }
        }
      ];
      return columns;
    }
  },
  methods: {
    //      批量删除
    batchDelete() {
      let _this = this;
      this.$confirm({
        title: "你确定要删除选中的客户信息吗?",
        content: "Some descriptions",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          _this.loading = true;
          // ajax request after empty completing
          setTimeout(() => {
            _this.loading = false;
            _this.dataSource = _this.dataSource.filter(
              item => !_this.selectedRowKeys.includes(item.key)
            );
            _this.selectedRowKeys = [];
          }, 1000);
        },
        onCancel() {
        }
      });
    },
    onSelectChange(selectedRowKeys) {
      this.selectedRowKeys = selectedRowKeys;
    },
    showExportModal() {},
    getDataFromServer() {
      //TODO:通过AJAX请求获得呼叫历史信息（如果仅显示该坐席的与该客户的呼叫记录，那么参数参数为坐席id+客户id）
      for (let i = 0; i < 25; i++) {
        this.dataSource.push({
          key: i,
          id: i,
          tag: `tag-${i}`,
          title: `title-${i}`,
          direction: `发送`,
          date_create: `2018-08-${i} ${i}:00:00`,
          user_create: `发布人-${i}`
        });
      }
    },
    deleteLocalHistory() {
      this.dataSource = [];
    },
    handleChange(pagination, filters, sorter) {
      this.filteredInfo = filters;
      this.sortedInfo = sorter;
    },
    onDelete(key) {
      let _this = this;
      this.$confirm({
        title: "你确定要删除该条记录吗?",
        content: "Some descriptions",
        okText: "确定",
        okType: "danger",
        cancelText: "取消",
        onOk() {
          _this.loading = true;
          // ajax request after empty completing
          setTimeout(() => {
            _this.loading = false;
            const dataSource = [..._this.dataSource];
            _this.dataSource = dataSource.filter(item => item.key !== key);
          }, 1000);
        },
        onCancel() {
        }
      });
    },
    showQueryModel() {
      this.$refs.dialogtreeQueryModal.showQueryModal();
    }
  }
};
</script>

<style>
</style>



